h1 {
  color: green;
}

#overlay {
  height: 100vh;
  width: 100%;
  z-index: 999;
  background-color: #00000080;
  position: fixed;
  top: 0;
  left: 0;
}

#drop-zone {
  width: 80%;
  height: 50%;
  text-align: center;
  background-color: #fff3;
  border: 5px dashed #fff;
  padding: 15px;
}

main {
  gap: 10px;
  padding: 10px;
}

.image {
  height: 200px;
  width: 200px;
  background-position: center;
  background-size: cover;
  border: 2px #d3d3d3;
  border-radius: 10px;
  flex-shrink: 0;
  transition-property: transform;
  transition-duration: .5s;
}

.image:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.loader {
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.loader .lds-facebook {
  width: 80px;
  height: 80px;
  display: inline-block;
  position: relative;
}

.loader .lds-facebook div {
  width: 16px;
  background: #1e90ff;
  animation: 1.2s cubic-bezier(0, .5, .5, 1) infinite lds-facebook;
  display: inline-block;
  position: absolute;
  left: 8px;
}

.loader .lds-facebook div:first-child {
  animation-delay: -.24s;
  left: 8px;
}

.loader .lds-facebook div:nth-child(2) {
  animation-delay: -.12s;
  left: 32px;
}

.loader .lds-facebook div:nth-child(3) {
  animation-delay: 0;
  left: 56px;
}

@keyframes lds-facebook {
  0% {
    height: 64px;
    top: 8px;
  }

  50%, 100% {
    height: 32px;
    top: 24px;
  }
}

